import { Injectable } from '@angular/core';

import { Coupon } from './../Common/Coupon';
import { Company } from './../common/Company';
import { Http } from '@angular/http';
import { HttpHeaders } from '@angular/common/http';
import Swal from 'sweetalert2';
import { Router} from '@angular/router';
import { timeout } from 'q';



@Injectable({
  providedIn: 'root'
})
export class SharedDataService {

  public coupons: Coupon[] = []
  public companies: Company[] = []
  request : Request;
  response : Response;

  constructor(private http: Http,private router: Router) {
  }


  public logout(request,response) {
    this.http.post('http://localhost:8080/companyservice/logout', request,response).subscribe(
      (resp) => {

        Swal(
          'You are logged out',
          ' You have been logout of the System.',
          'info'  
        )
      },

      //Error handeling
      (err) => {
        console.error('Error while add Coupon , Check you DB and Service!', err);
        Swal('Oops...', 'Something went wrong!', 'error')
      }
    );
  }


  /**
 * addCompanyService
 */
  public addCouponService(Coupon: Coupon) {
    let title = Coupon.title;
    this.http.post('http://localhost:8080/companyservice/createcoupon', Coupon).subscribe(
      (resp) => {
        Swal(
          'Coupon Created!',
          ' Coupon has been created.',
          'success'
        )
        this.getAllCouponsService();

      },

      //Error handeling
      (err) => {

        if (err.status == 409) {
          console.error('Error while create Coupon , there is a coupon in this name' + title +' , Choose another name !', err);
          Swal(
            {
              type: 'error',
              title: 'Cannot Create coupon!',
              text: 'A coupon with the same name : '+ title+ ' already exist, choose another name ',
              animation: true
            }
          )
        }
        else {
        console.error('Error while add Coupon , Check you DB and Service!', err);
        Swal('Oops...', 'Something went wrong!', 'error')
      }
    }
    );
  }

  public getCouponByIdService(id: any) {
    this.http.get('http://localhost:8080/companyservice/getcoupon/' + id).subscribe(
      (resp) => {
        //Drop all coupons from the coupons array
        this.coupons.length = 0;

        //Return one Coupon by ID from the API service
        this.coupons.push(resp.json());
        Swal(
          'Filter Coupon was Successful !',
          ' Managed to get Coupon by Id.' + id,
          'success'
        )
      },

      //Error handeling
      (err) => {
        if (err.status == 404) {
          console.error('Error while get Coupon By Type, Check you DB and Service!', err);
          Swal(
            {
              type: 'error',
              title: 'Cannot get coupons!',
              text: 'Did not find any Purchased coupon by id ' + id,
              animation: true
            }
          )
        }
        else {
          console.error('Error while Filter Coupon, Check you DB and Service!', err);
          Swal('Oops...', 'Something went wrong!', 'error')
        }
      }
    );
  }


  public getCouponByTypeService(type: any) {

    //Send the get Coupon By Type API to the Service
    this.http.get('http://localhost:8080/companyservice/getcouponbytype/' + type).subscribe(
      (resp) => {
        let temp = resp.json();
        this.coupons.length = 0;

        for (let item of temp) {
          this.coupons.push(item);
        }
        Swal(
          'Filter Coupon was Successful !',
          ' Managed to get Coupons by Type.',
          'success'
        )
      },

      //Error handeling
      (err) => {
        if (err.status == 500) {
          console.error('Error while get Coupon By Type, Check you DB and Service!', err);
          Swal(
            {
              type: 'error',
              title: 'Cannot get coupons!',
              text: 'Did not find any Purchased coupon by id ' + type,
              animation: true
            }
          )
        }
        else {
          console.error('Error while Filter Coupon, Check you DB and Service!', err);
          Swal('Oops...', 'Something went wrong!', 'error')
        }
      }
    );
  }

  public updateCouponService(id: any, coupons: Coupon) {
    //Send the Update API to the Service
    let url = 'http://localhost:8080/companyservice/updatecoupon/' + id;

    this.http.put(url, coupons).subscribe(
      (resp) => {

        Swal(
          'Updated!',
          ' Coupon has been updated.',
          'success'
        )

        //Reload all Coupons after delete Coupon action
        this.getAllCouponsService();
      },

      //Error handeling
      (err) => {
        console.error('Error while Deleting Coupon Check you DB and Service!', err);
        Swal('Oops...', 'Something went wrong!', 'error')
      }

    )


  }

  public removeCouponService(id: any) {
    Swal({
      title: 'Are you sure?',
      text: 'You will not be able to recover this Coupon!',
      type: 'warning',
      showCancelButton: true,
      showLoaderOnConfirm: true,
      confirmButtonText: 'Yes, delete it!',
      cancelButtonText: 'No, keep it'
    }).then((result) => {
      //If the User press Confirm button "Yes Delete it"
      if (result.value) {

        //Send the Delete API to the Service
        this.http.delete('http://localhost:8080/companyservice/removecoupon/' + id).subscribe(
          (resp) => {

            Swal(
              'Deleted!',
              ' Coupon has been deleted.',
              'success'
            )
            //Reload all Coupons after delete Coupon action
            this.getAllCouponsService();
          },

          //Error handeling
          (err) => {
            console.error('Error while Deleting Coupon Check you DB and Service!', err);
            Swal('Oops...', 'Something went wrong!', 'error')
          }

        )

        //If the User press Cancel button "No, keep it"
      } else if (result.dismiss === Swal.DismissReason.cancel) {
        Swal(
          'Cancelled',
          'Your Coupon is safe :)',
          'error'
        )
      }

    })
  }

  public getAllCouponsService() {
    this.http.get('http://localhost:8080/companyservice/getallcoupons').subscribe(
      (resp) => {
        //Create a temp coupons array
        let temp = resp.json();
        //drop all current coupons array
        this.coupons.length = 0;

        //Build items into new temp array
        for (let item of temp) {
          this.coupons.push(item);
        }
      },

      //Error handeling
      (err) => {
        console.error('Error while Deleting Coupon Check you DB and Service!', err);
        Swal('Oops...', 'Something went wrong!', 'error')
      }
    );
  }


}
