import { Component, OnInit } from '@angular/core';
import { Coupon } from './../../common/Coupon';
import {SharedDataService} from './../../services/shared-data.service';

@Component({
  selector: 'app-get-coupon',
  templateUrl: './get-coupon.component.html',
  styleUrls: ['./get-coupon.component.css']
})
export class GetCouponComponent implements OnInit {

  coupon : Coupon[] = [];

  constructor(private _dataService : SharedDataService) { 
    this.coupon = _dataService.coupons;
    this.getAllCoupons();
    // now the company is in the service
     
  }


  public getAllCoupons(){
    console.log('in');
    this._dataService.getAllCouponsService();   
     console.log(this.coupon);
  }
  
  public getCouponById(id:number){
    console.log(id);
    this._dataService.getCouponByIdService(id);   
     console.log(this.coupon);
  }



  ngOnInit() {
  }

}
